<template>
    <div id="app">
        <router-view></router-view>
    </div>
</template>

<script>
    export default {
        name: 'app',
    };
</script>

<style>
    @import url('https://fonts.googleapis.com/css?family=Merriweather|Roboto:400,700');

    #app {
        font-family: 'Merriweather', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        overflow: hidden;
        height: 100vh;
        width: 100%;
        display: flex;
        background-color: #181919;
        /*background: radial-gradient(circle farthest-side at 75% 50%, #7D4E74 30%, #3A1C71 100%);*/
        background: linear-gradient(60deg, #172329, #181919);
    }
</style>



