<template>
  <div class="landing" ref="landing">
    <div class="background">
      <Three :isActive="animationDetails.active" :step="animationDetails.step"></Three>
      <!--{{ animationDetails }}-->
    </div>
    <section id="slide-main">
      <h1>slide #1</h1>
      <div class="mouse">
        <div class="scroll-wheel"></div>
      </div>
    </section>
    <section id="slide-second" class="scroll-under">
      <h1>Welcome to PGS</h1>
      <h2>lorem ipsum dolor sit amet</h2>
    </section>
    <section id="slide-third" class="scroll-under">
      <p>hello world</p>
    </section>
  </div>
</template>

<script>
    /* eslint-disable */
    import {throttle, isFunction} from 'lodash';
    import Three from './Three';

    const scrollSettings = {
        sensivity: 30,
        duration: 600
    };

    const defaultPosition = {
        lat: 51.096474713,
        lng: 17.034752369,
    };

    let ticking = false;
    let currentSlideNumber = 0;
    let previouseSlideNumber = -1;
    let totalSlides = 0;
    let container;

    let animationDetails = {
        active: false,
        step: 0,
    };

    let slides = [
        {
            name: 'slide 1',
            init(dir) {
                const currentEl = document.getElementById('slide-main');
                currentEl.className = 'active';
                animationDetails.active = false;
            }
        },
        {
            name: 'slide 2',
            init(dir) {
                const prevEl = dir ? document.getElementById('slide-main') : document.getElementById('slide-second');
                prevEl.className = dir ? 'scroll-over' : 'scroll-under';
                animationDetails.active = true;
                animationDetails.step = 0;
            }
        },
        {
            name: 'slide 3',
            init(dir) {
                if (!dir) {
                    const prevEl = document.getElementById('slide-second');
                    prevEl.className = 'scroll-under';
                }

                animationDetails.active = true;
                animationDetails.step = 1;
            }
        },
        {
            name: 'slide 4',
            init(dir) {

                if (!dir) {
                    const prevEl = document.getElementById('slide-third');
                    prevEl.className = 'scroll-under';
                }

                const currentEl = document.getElementById('slide-second');
                currentEl.className = '';

                animationDetails.active = false;
            }
        },
        {
            name: 'slide 2',
            init(dir) {
                const prevEl = document.getElementById('slide-second');
                prevEl.className = 'scroll-over' ;

                const currentEl = document.getElementById('slide-third');
                currentEl.className = '';

                animationDetails.active = false;
            }
        }
    ];

    export default {
        name: 'landing',
        components: {
            Three,
        },
        data() {
            return {
                animationDetails: animationDetails,
                currentSlideNumber,
                position: defaultPosition,
            };
        },
        mounted() {
            window.addEventListener('wheel', throttle(fullPageScroll, 60), false);
            container = this.$refs.landing;
            totalSlides = slides.length;
            slides[0].init(1);

        },
        methods: {
            isActive: function() {
                return animationDetails.active;
            }
        },
    };

    function fullPageScroll(e) {
        const delta = e.wheelDelta;

        if (ticking != true) {
            if (delta <= -scrollSettings.sensivity) { //nextItem
                //Down scroll
                ticking = true;
                if (currentSlideNumber !== totalSlides - 1) {
                    previouseSlideNumber = currentSlideNumber;
                    currentSlideNumber++;
                }
                scrollItems(true);
                slideDurationTimeout(scrollSettings.duration);
            }

            if (delta >= scrollSettings.sensivity) { //prevItem
                //Up scroll
                ticking = true;
                if (currentSlideNumber !== 0) {
                    previouseSlideNumber = currentSlideNumber;
                    currentSlideNumber--;
                }

                scrollItems(false);
                slideDurationTimeout(scrollSettings.duration);
            }
        }
    }

    function slideDurationTimeout(slideDuration) {
        setTimeout(function() {
            ticking = false;
        }, slideDuration);
    }

    function scrollItems(dir) {
        /*console.log('scrollItems()', currentSlideNumber);
         slides.forEach((slide, slideIdx) => {
         if (slideIdx === currentSlideNumber) {
         addClass(slide.el, 'active');
         } else {
         removeClass(slide.el, 'active');
         }
         })*/
        const slide = slides[currentSlideNumber];
        slide.init(dir);

        console.log('previouseSlideNumber', previouseSlideNumber);
        console.log('currentSlideNumber', currentSlideNumber);
    }

    // --- class helpers
    function hasClass(el, className) {
        if (el.classList) {
            return el.classList.contains(className);
        } else {
            return !!el.className.match(new RegExp('(\\s|^)' + className + '(\\s|$)'))
        }
    }
    function addClass(el, className) {
        if (el.classList) {
            el.classList.add(className);
        } else if (!hasClass(el, className)) {
            el.className += " " + className;
        }
    }
    function removeClass(el, className) {
        if (el.classList) {
            el.classList.remove(className);
        } else if (hasClass(el, className)) {
            var reg = new RegExp('(\\s|^)' + className + '(\\s|$)');
            el.className = el.className.replace(reg, ' ');
        }
    }

</script>

<style>
    /* global styles */
    html, body {
        height: 100vh;
        overflow: hidden;
    }
</style>

<style scoped lang="scss">
  $transition-speed: 1.2s;

  @mixin transition($time, $property: all, $easing: ease-in) {
    transition: $property $time $easing;
  }

  .landing {
      height: 100vh;
      overflow: hidden;
  }

  .background {
    /*display: none;*/
    position: fixed;
    height: 100vh;
    width: 100%;
    color: #fff;
  }

  section {
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    transform: translate3d(0, 0, 0);
    @include transition($transition-speed, all, cubic-bezier(0.22, 0.44, 0, 1));
  }

  #slide-main {
      background: url('/static/wpt.jpg');
      background-size: cover;
  }

  #slide-second {
      background: none;
      color: #fff;

      h1 {
        font-size: 40px;
        position: absolute;
        left: 10%;
        top: 20%;
      }
  h2 {
    font-size: 30px;
    position: absolute;
    left: 10%;
    top: 30%;
  }
  }

  #slide-main::after {
      position: absolute;
      width: 100vw;
      height: 100vh;
      content: "";
      opacity: 0.25;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background-color: #181919;
  }

  section.active {
    transform: translateY(0);
  }

  @for $i from 1 to (10) {
    section:nth-of-type(#{$i}) {
      z-index: (100 + 1) - $i;
    }
  }

  $colors: #2ecc71 #3498db #9b59b6 #f1c40f #e67e22 #ecf0f1;

  @each $current-color in $colors {
  $i: index($colors, $current-color);
    section:nth-child(#{$i}) {
      background-color: $current-color;
    }
  }

  .mouse {
    position: absolute;
    bottom: 20px;
    width: 3px;
    padding: 10px 15px;
    height: 35px;
    border: 2px solid #fff;
    border-radius: 25px;
    opacity: 0.75;
    box-sizing: content-box;
  }

  .scroll-wheel {
    width: 3px;
    height: 10px;
    border-radius: 25%;
    background-color: #fff;
    animation-name: scroll;
    animation-duration: 1.2s;
    animation-timing-function: cubic-bezier(.15,.41,.69,.94);
    animation-iteration-count: infinite;
  }

  @keyframes scroll {
    0% { opacity: 0; }
    10% { transform: translateY(0); opacity: 1; }
    100% { transform: translateY(15px); opacity: 0;}
  }

  section.up-scroll {
    transform: translate3d(0, -(100vh), 0);
  }

  section.down-scroll {
    transform: translate3d(0, (100vh), 0);
  }



  section.scroll-under {
    transform: translate3d(0, (100vh), 0);
  }
  section.scroll-over  {
    transform: translate3d(0, -(100vh), 0);
  }
  section.scroll-active {
    transform: translate3d(0, 0, 0);
  }



</style>



