<template>
    <div class="Scene" ref="scene">
    </div>
</template>

<script>
    /* eslint-disable */
    import * as THREE from 'three';
    import TWEEN from 'tween';
    import moment from 'moment';
    import suncalc from 'suncalc';
    import {throttle} from 'lodash';

    import TrackballControls from '../lib/TrackballControls';
    import OrbitControls from '../lib/OrbitControls';

    const RADIUS = 35;
    const SPEED = 0.05;

    let scene;
    let camera;
    let controls;
    let renderer;
    let time;
    let CONTAINER;
    let WIDTH;
    let HEIGHT;

    let light;
    let lightHelper;

    let mouseX = 0;
    let mouseY = 0;
    let conatinerHalfX;
    let conatinerHalfY;

    let complex;

    const point0 = new THREE.Vector3(5, 0, -5);
//    const cameraStart = new THREE.Vector3(-20, 5, 30);
//    const cameraEnd = new THREE.Vector3(2.80944512961746, 14.220028358545097, 28.24894378535977);

    const positions = [
            [new THREE.Vector3(-20, 5, 30), new THREE.Vector3(2.809, 14.220, 28.248)],
            [new THREE.Vector3(2.809, 14.220, 28.248), new THREE.Vector3(2.809, 25.220, 28.248)],
    ];

    let currentStep = 0;
    let cameraMove;
    let moveProgress = 0.5;

    function initRenderer() {
        renderer = new THREE.WebGLRenderer({
            alpha: true,
            antialias: true,
        });
        renderer.shadowMap.soft = true;
        renderer.shadowMap.enabled = true;
        renderer.setSize(WIDTH, HEIGHT);

        CONTAINER.appendChild(renderer.domElement);
    }

    function addCamera() {
        camera = new THREE.PerspectiveCamera(50, WIDTH / HEIGHT, 1, 10000);
        camera.position.copy(getPointInBetweenByPerc(positions[currentStep][0], positions[currentStep][1], 0.2));
        camera.lookAt(point0);

//        camera.position.z = 150;
//        camera.position.set(-2.306256570696724, 4.925962611183612, 10.306098033269578);
//        camera.rotation.set(-0.17513980130096474, -0.6437566908202768, -0.1498857892062786);

//        camera.matrix.fromArray([0.6870609761528993, -0.007929235183712116, 0.7265564962735014, 0, 0.022765901172780427, 0.9996844324293253, -0.010618347434937725, 0, -0.7262430232309405, 0.023836165544383094, 0.6870246781743174, 0, -3.435323831631102, 3.253967627726094, 7.008789812907256, 1]);
//        camera.matrix.decompose(camera.position, camera.quaternion, camera.scale);

//        controls = new OrbitControls(camera, CONTAINER);
//        controls.noPan = true;
//        controls.minZoom = 10;
//        controls.maxPolarAngle = deg2rad(80);
//        controls.enableKeys = false;
//        controls.minDistance = 0;
//        controls.maxDistance = 90;
//        controls.zoomSpeed = 2;
//        controls.target.copy(point0);
//
//        controls = new TrackballControls(camera);
//        controls.zoomSpeed = 0.1;
//        loadCamera();
    }

    function loadModel() {
        const scale = 5;
//        const loader = new THREE.ObjectLoader();
        const loader = new THREE.JSONLoader();
        complex = new THREE.Object3D();

        loader.load(
                '/static/models/wpt.json',
                function(object) {
                    console.log('object', );

                    const edgesMat = new THREE.LineBasicMaterial({
                        color: 0x1abc9c,
                        linewidth: 2
                    });

                    const wireframeMat = new THREE.MeshBasicMaterial(
                            {
                                color: 0x1abc9c,
                                side: THREE.DoubleSide,
                                transparent: true,
                                opacity: 0.25,
                                wireframe: true,
                            });

                    const buidlingMat = new THREE.MeshBasicMaterial(
                            {
                                color: 0x1abc9c,
                                side: THREE.DoubleSide,
                                transparent: true,
                                opacity: 0.2,
                            });

                    const edgesGeo = new THREE.EdgesGeometry(object);
                    const edges = new THREE.LineSegments(edgesGeo, edgesMat);
                    const wireframe = new THREE.Mesh(object, wireframeMat);
                    const building = new THREE.Mesh(object, buidlingMat);

                    [edges, wireframe, building].forEach(obj => {
                        obj.scale.set(scale, scale, scale);
                        obj.position.set(10, 0, 10);
                    });

                    complex.add(edges);
                    complex.add(wireframe);
                    complex.add(building);

                    scene.add(complex);
                }
        );


    }

    function loadGeometry() {

        const sphereGeometry = new THREE.OctahedronGeometry(1, 3);
        const sphereMaterial = new THREE.MeshBasicMaterial({color: 0xFFFFFF});

        const sphere = new THREE.Mesh(sphereGeometry, sphereMaterial);
        sphere.position.copy(point0);

        scene.add(sphere);
    }

    function addLight() {
        light = new THREE.DirectionalLight(0xf1c40f, 1, 100);
        light.position.set(15, 25, 0);
        light.target.position.set(0, 0, 0);
        light.castShadow = true;

        const targetObject = new THREE.Object3D();
        scene.add(targetObject);
        light.target = targetObject;
        light.shadowMapWidth = 2048;
        light.shadowMapHeight = 2048;
        light.shadowCameraLeft = -50;
        light.shadowCameraBottom = -50;
        light.shadowCameraRight = 50;
        light.shadowCameraTop = 50;
        light.shadowBias = -0.001;
        scene.add(light);

        const ambientLight = new THREE.AmbientLight(0xFFFFFF); // soft white light
        scene.add(ambientLight);

        lightHelper = new THREE.DirectionalLightHelper(light, 5);
        scene.add(lightHelper);

        const cameraHelper = new THREE.CameraHelper(light.shadow.camera);
        scene.add(cameraHelper);
    }

    function addSceneHelpers() {
        const axisHelper = new THREE.AxisHelper(10);
        scene.add(axisHelper);

        const gridHelper = new THREE.GridHelper(100, 100);
        scene.add(gridHelper);
    }

    function addAxisLabels(font) {
        const offsetXYZ = RADIUS + 10;
        const offsetNSEW = RADIUS + 5;
        addText('X', [offsetXYZ, 0, 0], [0, 0, 0], font, 0xe74c3c);
        addText('Y', [0, offsetXYZ, 0], [0, 0, 0], font, 0x2ecc71);
        addText('Z', [0, 0, offsetXYZ], [0, 0, 0], font, 0x3498db);

        addText('N', [offsetNSEW, 0, 0], [-Math.PI / 2, 0, 0], font, 0xf1c40f);
        addText('S', [-offsetNSEW, 0, 0], [-Math.PI / 2, 0, 0], font, 0xf1c40f);
        addText('E', [0, 0, offsetNSEW], [-Math.PI / 2, 0, 0], font, 0xf1c40f);
        addText('W', [0, 0, -offsetNSEW], [-Math.PI / 2, 0, 0], font, 0xf1c40f);
        render();
    }

    function onWindowResize() {
        renderer.setSize(0, 0);

        WIDTH = CONTAINER.offsetWidth;
        HEIGHT = CONTAINER.offsetHeight;
        conatinerHalfX = WIDTH / 2;
        conatinerHalfY = HEIGHT / 2;

        camera.aspect = WIDTH / HEIGHT;
        camera.updateProjectionMatrix();
        renderer.setSize(WIDTH, HEIGHT);

        render();
    }

    function onDocumentMouseMove( event ) {
        mouseX = ( event.clientX - conatinerHalfX ) / 10;
        mouseY = ( event.clientY - conatinerHalfY ) / 10;
        moveProgress = event.clientX / WIDTH;

        let cameraStart = positions[currentStep][0];
        let cameraEnd =  positions[currentStep][1];

        cameraMove = getPointInBetweenByPerc(cameraStart, cameraEnd, moveProgress);

    }

    function handleKeyboard(e) {
        switch (e.keyCode) {
            case 83: saveCamera();
                break;
            case 65: loadCamera();
                break;
        }
    }

    function loadCamera() {
        const savedCamera = JSON.parse( localStorage.getItem( 'savedCamera' ) );
        if (savedCamera){
            camera.position.copy( savedCamera.cameraPosition );
            camera.lookAt(savedCamera.targetPosition);
            camera.lookAt(point0);

            controls.target.copy( savedCamera.targetPosition );
        }
    }

    function saveCamera() {
        localStorage.savedCamera = JSON.stringify({
            cameraPosition: camera.position,
            targetPosition: controls.target
        });
    }

    function render() {

        renderer.render(scene, camera);
    }

    function initEvents() {
//        controls.addEventListener('change', render);
        window.addEventListener('resize', onWindowResize, false);
        document.addEventListener('mousemove', throttle(onDocumentMouseMove, 50), false );
        document.body.addEventListener('keydown', handleKeyboard);
    }


    function rad2deg(rad) {
        return rad * (180 / Math.PI);
    }

    function deg2rad(deg) {
        return deg * (Math.PI / 180);
    }

    function getPointInBetweenByPerc(pointA, pointB, percentage) {

        var dir = pointB.clone().sub(pointA);
        var len = dir.length();
        dir = dir.normalize().multiplyScalar(len*percentage);
        return pointA.clone().add(dir);

    }

    export default {
        name: 'scene',
        props: ['isActive', 'step'],
        data() {
            return {};
        },
        computed: {
            hour: function() {
                var coeff = 1000 * 60 * 10;
                var rounded = new Date(Math.round(this.time / coeff) * coeff);
                return moment(rounded).format('HH:mm:ss');
            },
            date: function() {
                return moment(this.time).format('DD/MM/YYYY');
            }
        },
        mounted() {
            if (this.$refs.scene.querySelector('canvas') === null) {
                CONTAINER = this.$refs.scene;
                WIDTH = CONTAINER.offsetWidth;
                HEIGHT = CONTAINER.offsetHeight;

                conatinerHalfX = WIDTH / 2;
                conatinerHalfY = HEIGHT / 2;

                this.init();
            }
        },
        watch: {
            isActive: function(val) {
              console.log('isActive', val);
            },
            step: function(val) {
                currentStep = val;

                let cameraStart = positions[currentStep][0];
                let cameraEnd =  positions[currentStep][1];
                cameraMove = getPointInBetweenByPerc(cameraStart, cameraEnd, moveProgress);
            }
        },
        methods: {
            animate() {
                requestAnimationFrame(this.animate);
                if (this.isActive) {
                    if (!!cameraMove) {
                        let dir = new THREE.Vector3().subVectors(cameraMove, camera.position);
                        camera.position.x += dir.x * SPEED;
                        camera.position.y += dir.y * SPEED;
                        camera.position.z += dir.z * SPEED;
                        camera.lookAt(point0);
                    }

//                controls.update();
                    render();
                }
            },

            init() {
                scene = new THREE.Scene();
                this.time = new Date().valueOf();

                loadModel();
//                loadGeometry();

                addCamera();
//                addSceneHelpers();

                initRenderer();
                initEvents();

                render();
                this.animate();
            }
        },
    };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .Scene {
        flex: 1;
        position: relative;
        z-index: 10;
        /*background-color: #181919;*/
        position: absolute;
        width: 100vw;
        height: 100vh
    }

    .Scene::after {
        content: "";
        /*background: url('/static/wpt.jpg');*/
        background-size: cover;
        opacity: 0.25;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        position: absolute;
        z-index: 0;
    }
</style>


